.contact-container{
    width: 100%;
    
    margin-right: auto;
    margin-left: auto;
}

.grid-container {
    display: grid;
    grid-template-columns: 1fr 1fr;   
}

.grid-child{
    width: 90%;
}

/* Contact Form */
.contactform{
    background-color: rgb(162, 192, 190);
    margin: 0px 0px 0px 70px;
    height: 680px;
    display: block;
    text-align: left;
}

.form-label{
    color:  rgb(123, 129, 128);
    font-family: cursive;
    font-variant: small-caps;
}

.contactform-head{
    padding: 40px 0px 0px 48px;
    color:  rgb(102, 109, 108);
    font-family: 'Courier New', Courier, monospace;
    font-variant: small-caps;
    font-weight: 550;
}

.form-input{
    width: 10px;
}

.form{
    align-items: center;
}

/* contact detail */
.contactdetail{
    background-color: rgb(108, 117, 125);
    text-align: left;
    height: 680px;
}

.contactus-head{
    padding: 10px 0px 0px 35%;
    color: white;
    font-family: 'Courier New', Courier, monospace;
    font-variant: small-caps;
    font-weight: 550;
    font-size: 50px;
}

.head{
    color:  white;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    padding-left: 30%;
}

.text{
    color: rgb(206, 206, 206);
    padding-left: 30%;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.contact-paw{
    padding: 150px 0px 0px 45%;
}

