.blogs-container{
    margin-top: 5rem;
}

.blog-date{
    color: red;
    padding-top: 30px;
}

.blog-head{
    font-family: fantasy;
    font-variant: small-caps;
    font-style: italic;
}

.blog-footer{
    font-family: cursive;
    font-style: oblique;
    color: grey;
}

@media only screen and (min-width: 800px){
    .blogs-div{
        width: 80%;
        margin: 0 auto;
        border: 2px solid lightgrey;
        box-shadow: 5px 10px 18px #888888;
        border-radius: 5px;
    }

    .blog-photo{
        width: 500px;
        height: 400px;
    }

    .blog-content{
        width: 80%;
        text-align: center;
        margin-left: 7rem;
        font-family: 'Courier New', Courier, monospace;
        font-style: italic;
    }
}

@media only screen and (max-width: 800px){
    .blogs-div{
        margin: 0 auto;
        border: 2px solid lightgrey;
        box-shadow: 5px 10px 18px #888888;
        border-radius: 5px;
    }

    .blog-photo{
        width: 330px;
        height: 200px;
    }

    .blog-content{
        text-align: center;
        font-family: 'Courier New', Courier, monospace;
        font-style: italic;
    }
}