.about-container{
    background-color: rgb(162, 192, 190);
}

.about-dogpaw{
    padding-top: 80px;
}

.about-content{
    align-items: flex-start;
    font-family: 'Courier New', Courier, monospace;
    font-style: italic;
    font-weight: 500;
}

.inspire-container{
    background-color: cadetblue;
}

.inspire-note-head{
    font-weight: bolder;
    font-variant: small-caps;
}

@media only screen and (min-width: 800px){
    .about-image{
        height: 700px;
        width: 450px;
    }

    .about-head{
        font-family: fantasy;
        font-variant: small-caps;
        font-size: 60px;  
    }

    .inspire-note{
        font-family: sans;
        font-style: italic;
        font-weight: 500;
        font-size: larger;
    }
}

@media only screen and (max-width: 800px){
    .about-image{
        height: 700px;
        width: 300px;
    }

    .about-head{
        font-family: fantasy;
        font-variant: small-caps;
        font-size: 40px;  
    }

    .inspire-note{
        font-family: sans;
        font-style: italic;
        font-weight: 500;
        font-size: large;
    }
}