.about-p{
    font-family: cursive;
    font-style: oblique;
    font-weight: lighter;
}

.about-h2{
    padding-top: 20px;;
    font-family: fantasy;
    font-variant: small-caps;
    font-size: 50px;
}

.about-span{
    font-family: Arial, Helvetica, sans-serif;
    font-style: oblique;  
}

.home-p{
    padding: 5px 0px 0px 20px;
    font-family: cursive;
    font-variant: normal;
    font-style: oblique;
    color: rgb(175, 173, 173);
}

/* for desktop */
@media only screen and (min-width: 800px){
    .home-dog1{
        padding-left: 50px;
        height: 680px;
    }

    .home-h2{
        padding: 30% 0px 0px 80px;
        font-family: fantasy;
        font-variant: small-caps;
        font-size: 80px;
    }

    .card-image{
        height: 250px;
    }

    .char-card{
        height: 28rem;
        width: 25rem;
    }

    .char-row{
        padding-left: 2rem;
    }
}

/* for mobile phones and tablets */
@media only screen and (max-width: 800px) {
    .home-container{
        margin-top: 20px;
    }

    .home-h2{
        padding: 20% 0px 0px 80px;
        font-family: fantasy;
        font-variant: small-caps;
        font-size: 40px;
    }

    .home-dog1{
        padding-left: 50px;
        height: 680px;
        width: 280px;
    }

}
