.footer-container{
    background-color: rgb(43, 43, 43);
    text-align: left;
    
}

/* Quick Links styling */
.footer-head{
    color: rgb(158, 158, 158);
    padding-top: 50px;
    padding-left: 20%;
}

.links{
    color: rgb(94, 94, 94);
    text-decoration: none;
    padding-left: 20%;
    padding-top: 30px;
}

.links :hover{
    color: rgb(158, 158, 158);
    text-decoration: none;
}

/* Social Media links styling */
.social-links{
    color: rgb(94, 94, 94);
    text-decoration: none;
    padding-left: 20%;
}


/* Copyright section styling */
.border-top{
    border-top: 1px solid;
    
}
.text-center{
    text-align: center;
}
.copyright{
    color: rgb(109, 109, 109);
}

